body {
  margin: 0;
}

.interviewee{
  position: fixed;
  bottom: 0;
  right: 0;
  line-height: 1em;
  padding: .5em .8em;
  background: #00000066;
  color: #fff;
  opacity: .8;
  border-radius: 1em 0 0 0;
  z-index: 9;
}

.container {
  max-width: 1280px;
  width: 85%;
  height: 70vh;
  margin: 20px auto 0;
  background-color: #f8fac8;
  border: 2px solid #c4c783;
  border-radius: 10px;
  padding: 20px;
}

@media (min-width: 786px) {
  .container {
    width: 65%;
    height: 500px;
  }
}